<template>
  <main>
    <h1>Private Area</h1>
    <p>
      Dear {{ info.nameSurname.value }}, below are reported your information,
      which you can change, except for your username. To save your changes, just
      click <a @click="commitChanges()">here</a>.
      <span v-if="info.totUnseenFiles === 0"
        >All files have been read, good job!.</span
      >
      <span v-else
        >We remind you that there are files you have not downloaded yet.</span
      >
    </p>

    <table v-if="info.length !== 0">
      <colgroup>
        <col />
        <col />
      </colgroup>
      <tr>
        <th>Username</th>
        <td>{{ info.username }}</td>
      </tr>
      <tr>
        <th>Name, Surname</th>
        <td>
          <input
            type="text"
            v-model="info.nameSurname.value"
            v-bind:class="[info.nameSurname.active ? 'active' : 'inactive']"
            @click="setWritable(info.nameSurname)"
            @keyup.enter="setReadOnly(info.nameSurname)"
            readonly
          />
        </td>
      </tr>
      <tr>
        <th>Email</th>
        <td>
          <input
            type="text"
            v-model="info.email.value"
            v-bind:class="[info.email.active ? 'active' : 'inactive']"
            @click="setWritable(info.email)"
            @keyup.enter="setReadOnly(info.email)"
            readonly
          />
        </td>
      </tr>
      <tr>
        <th>Password</th>
        <td>
          <input
            type="password"
            v-model="info.password.value"
            v-bind:class="[info.password.active ? 'active' : 'inactive']"
            @click="[showPassword(), setWritable(info.password)]"
            @keyup.enter="[hidePassword(), setReadOnly(info.password)]"
            readonly
          />
        </td>
      </tr>
      <tr>
        <th>Total Uploaders</th>
        <td>{{ info.totUploaders }}</td>
      </tr>
      <tr>
        <th>Total Files</th>
        <td>{{ info.totFiles }}</td>
      </tr>
      <tr>
        <th>Total Unseen Files</th>
        <td>{{ info.totUnseenFiles }}</td>
      </tr>
      <tr>
        <th>Total Seen Files</th>
        <td>{{ info.totSeenFiles }}</td>
      </tr>
    </table>
    <form :action="logoutLink()" method="post">
      <input type="submit" value="Logout" />
    </form>
  </main>
</template>

<script>
import Axios from "axios";

export default {
  name: "PrivateArea",
  data: function () {
    return {
      info: {
        username: "loading...",
        email: { value: "loading...", active: false },
        nameSurname: { value: "loading...", active: false },
        password: { value: "", active: false },
        totFiles: "loading...",
        totSeenFiles: "loading...",
        totUnseenFiles: "loading...",
        totUploaders: "loading...",
      },
    };
  },
  created: function () {
    Axios.get(`${process.env.VUE_APP_APIROOT}consumerarea/consumer`)
      .then((response) => {
        this.info.username = response.data.username;
        this.info.email = { value: response.data.email, active: false };
        this.info.nameSurname = {
          value: response.data.nameSurname,
          active: false,
        };
        this.info.password = { value: response.data.password, active: false };
        this.info.totFiles = response.data.totFiles;
        this.info.totSeenFiles = response.data.totSeenFiles;
        this.info.totUnseenFiles = response.data.totUnseenFiles;
        this.info.totUploaders = response.data.totUploaders;
      })
      .catch((error) => console.log(error));
  },
  methods: {
    logoutLink: function () {
      return `${process.env.VUE_APP_HOST}/logout`;
    },
    showPassword: function () {
      event.target.type = "text";
    },
    hidePassword: function () {
      event.target.type = "password";
    },
    setReadOnly: function (property) {
      event.target.readOnly = true;
      property.active = false;
    },
    setWritable: function (property) {
      event.target.readOnly = false;
      property.active = true;
    },
    commitChanges: function () {
      let changes = {
        username: this.info.username,
        nameSurname: this.info.nameSurname.value,
        email: this.info.email.value,
        password: this.info.password.value,
      };
      Axios.put(`${process.env.VUE_APP_APIROOT}consumerarea/consumer`, changes)
        .then(() => alert("changes committed correctly!"))
        .catch(() => alert("something went wrong..."));
    },
  },
};
</script>

<style scoped>
h1 {
  font-size: 6vh;
}
p {
  width: 95%;
  margin-right: auto;
  margin-left: auto;
}

p a {
  color: blue;
  text-decoration: underline;
}

p a:hover {
  cursor: pointer;
}

table {
  margin-left: auto;
  margin-right: auto;
}

table colgroup col {
  width: 200px;
}

table tr,
table tfoot tr {
  height: 5vh;
  min-height: 100%;
}

table tr th {
  text-align: left;
  padding-left: 1vw;
}

table tr td,
table tr td input {
  text-align: center;
}

table tr td input[type="text"]:focus,
table tr td input[type="password"]:focus {
  outline: none;
}

form {
  width: 400px;
  margin: 10px auto;
  display: flex;
  justify-content: flex-end;
}

form input[type="submit"] {
  border: 3px solid black;
  border-radius: 5px;
  padding: 5px 10px;
  width: 100px;
  height: 40px;
  color: rgb(214, 139, 0);
  background-color: rgba(0, 0, 0, 0.9);
  font-weight: bold;
  font-size: 110%;
  font-family: "Archivo Black";
}

.inactive {
  background-color: inherit;
  border: 2px solid whitesmoke;
}
.active {
  border: 2px solid black;
  border-radius: 10px;
}

@media screen and (orientation: portrait) {
  table {
    width: 95%;
  }

  table tr th,
  table tr td,
  table tr td input {
    font-size: 4vw;
    overflow-x: auto;
    white-space: nowrap;
  }

  form {
    width: 95%;
  }

  form input[type="submit"] {
    border: 3px solid black;
    border-radius: 5px;
    padding: 0;
    width: 30%;
    height: 4vh;
    color: rgb(214, 139, 0);
    background-color: rgba(0, 0, 0, 0.9);
    font-weight: bold;
    font-size: 2vh;
    font-family: "Archivo Black";
  }
}
</style>