<template>
  <main v-if="currentUploader!==null">
    <UploaderItem :uploader="currentUploader" class="uploaderItem" />
    <FileTable v-if="currentFiles!==undefined" :files="currentFiles" class="FileTable" />
  </main>
</template>

<script>
import UploaderItem from "@/components/UploaderItem";
import FileTable from "@/components/FileTable";

export default {
  name: "Uploader",
  components: {
    UploaderItem,
    FileTable,
  },
  props: ["uploaders"],
  methods: {
    compareElementsBySeen: function (a, b) {
      if (a.seen !== "" && b.seen === "") {
        return -1;
      } else if (a.seen === "" && b.seen !== "") {
        return 1;
      } else {
        return 0;
      }
    },
    compareElementsByUploadDate: function (a, b) {
      let first = new Date(a.uploaded);
      let second = new Date(b.uploaded);
      return second - first;
    },
  },
  computed: {
    currentFiles: function () {
      if (this.uploaders.length > 0) {
        //array ordinato da passare dei soli files
        let filesSorted = this.uploaders
          .filter((element) => element.username === this.$route.params.username)
          .map((element) => element["files"])[0];

        //files seen and unseen
        let filesUnseen = filesSorted.filter((element) => element.seen === "");
        let filesSeen = filesSorted.filter((element) => element.seen !== "");

        //sort the the arrays
        filesUnseen.sort(this.compareElementsByUploadDate);
        filesSeen.sort(this.compareElementsByUploadDate);

        //return a sorted array
        return filesUnseen.concat(filesSeen);
      } else {
        return null;
      }
    },
    currentUploader: function () {
      if (this.uploaders.length > 0) {
        return this.uploaders.filter(
          (element) => element.username === this.$route.params.username
        )[0];
      } else {
        return null;
      }
    },
  },
};
</script>

<style scoped>

main img {
  width: 10vw;
  border: 2px solid black;
  border-radius: 500px;
}

.FileTable {
  margin-top: 5px;
  margin-bottom: 5px;
}
</style>