<template>
  <section>
    <div>
      <input type="text" v-model="hashtag" placeholder="filter by hashtag..." />
    </div>
    <table>
      <thead>
        <tr>
          <th>Name</th>
          <th>Hashtags</th>
          <th>Uploaded</th>
          <th>Seen</th>
          <th>Download</th>
        </tr>
      </thead>
      <tbody v-if="filesFilter.length !== 0">
        <tr v-for="(file, index) in filesFilter" v-bind:key="index">
          <td>{{ file.name }}</td>
          <td>{{ file.hashtags.join(", ") }}</td>
          <td>{{ file.uploaded }}</td>
          <td v-if="file.seen !== ''">{{ file.seen }}</td>
          <td v-else>unseen file</td>
          <td>
            <a v-bind:href="file.link">
              <img src="download_icon.png" alt="download" />
            </a>
          </td>
        </tr>
      </tbody>
      <tbody
        v-else
        style="
          display: flex;
          flex-direction: row;
          justify-content: center;
          align-items: center;
        "
      >
        <em style="text-align: center"
          >there are no files uplaoded for you yet...</em
        >
      </tbody>
    </table>
  </section>
</template>

<script>
export default {
  props: ["files"],
  data: function () {
    return {
      hashtag: "",
    };
  },
  computed: {
    filesFilter: function () {
      if (this.hashtag !== "") {
        return this.files.filter((file) => {
          for (let i = 0; i < file.hashtags.length; i++) {
            if (
              file.hashtags[i]
                .toLowerCase()
                .includes(this.hashtag.toLowerCase())
            )
              return true;
          }
          return false;
        });
      } else {
        return this.files;
      }
    },
  },
};
</script>

