<template>
  <div id="app">
    <Header />
      <router-view v-bind:uploaders="uploaders" />
  </div>
</template>


<script>
import Axios from "axios";
import Header from "./components/Header";

export default {
  name: "App",
  components: {
    Header,
  },
  data: function () {
    return {
      uploaders: {},
    };
  },
  created: function () {
    Axios.get(`${process.env.VUE_APP_APIROOT}consumerarea/consumernews`)
      .then((response) => {
        this.uploaders = response.data;

        let infoFiles = [];
        this.uploaders.forEach((uploader) => {
          let infoFile = { unseenFiles: 0, username: uploader.username };
          uploader.files.forEach((file) => {
            if (file.seen === "") {
              infoFile.unseenFiles++;
            }
          });
          infoFiles.push(infoFile);
        });

        let forward = { username: "", change: false };
        infoFiles.forEach((info) => {
          if (
            info.unseenFiles !== 0 &&
            forward.change === false &&
            forward.username !== "none"
          ) {
            forward.username = info.username;
            forward.change = true;
          } else if (info.unseenFiles !== 0 && forward.change === true) {
            forward.change = false;
            forward.username = "none";
          }
        });
        if (forward.change) {
          this.$router.push({
            name: "Uploader",
            params: { username: forward.username },
          });
        }
      })
      .catch((error) => console.log(error));
  },
};
</script>

