<template>
  <article>
    <img v-bind:src="this.logo" alt="logo_uploader" />
    <h1>Uploader {{ uploader.username }}</h1>
    <p v-if="unreadFiles !== 0">Has sent {{ unreadFiles }} new files to you!</p>
    <p v-else>has no new files for you...</p>
  </article>
</template>

<script>
export default {
  props: ["uploader"],
  name: "UploaderCard",
  computed: {
    unreadFiles: function () {
      let numberUnreadFiles = 0;
      if (this.uploader.files !== undefined) {
        this.uploader.files.forEach((element) => {
          if (element.seen === "") numberUnreadFiles++;
        });
        return numberUnreadFiles;
      } else {
        return 0;
      }
    },
    logo: function () {
      if (this.uploader.logo === "") {
        return "/default_profile_picture.png";
      } else {
        return `data:image/jpeg;base64, ${this.uploader.logo}`;
      }
    },
  },
};
</script>

<style scoped>
@media screen and (orientation: landscape) {
  article {
    box-sizing: border-box;
    border: 3px solid orange;
    border-radius: 20px;
    background-color: rgba(0, 0, 0, 0.83);
    padding-top: 3vh;
    width: 100%;
    height: 100%;
    display: flex;
    flex-flow: column nowrap;
  }

  article img {
    width: 150px;
    height: 150px;
    margin: 4vh auto 6vh auto;
    border: 3px solid rgb(214, 139, 0);
    border-radius: 50%;
    box-sizing: border-box;
  }

  article h1 {
    text-decoration: none;
    color: rgb(214, 139, 0);
  }

  article p {
    text-align: center;
    color: white;
  }

  article:hover {
    border-color: red;
  }
}

@media screen and (orientation: portrait) {
  article {
    box-sizing: border-box;
    border: 3px solid orange;
    border-radius: 20px;
    background-color: rgba(0, 0, 0, 0.83);
    padding: 2vw 1vh;
    width: 100%;
    height: 60%;
    display: flex;
    flex-flow: column nowrap;
  }

  article img {
    width: 100px;
    height: 100px;
    margin: 0 auto;
    border: 3px solid rgb(214, 139, 0);
    border-radius: 50%;
    box-sizing: border-box;
  }

  article h1 {
    text-decoration: none;
    color: rgb(214, 139, 0);
    margin-top: 1vh;
    font-size:110%;
  }

  article p {
    text-align: center;
    color: white;
    font-size: 90%;
  }
}
</style>